import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import { Flex, Box, Text } from "rebass"
import GatsbyLink from 'gatsby-link'
import styled from '@emotion/styled/macro';

const StyledBox = styled(Box)`
  & img {
    position: relative;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 80%;
    object-fit: contain;
    object-position: center center;
    opacity: 1;
    transition-property: all;
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    transition-delay: 0s;
  }

  &:hover img {
    transform: scale(1.05);
    transition-property: all;
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    transition-delay: 0s;
  }
`;

const ProductRelated = (props) => {
  const { prodType, prodId } = props;

  const data = useStaticQuery(graphql`
    {
      relatedProducts: allShopifyProduct(limit: 4) {
        nodes {
          handle
          id
          title
          productType
          fields {
            shopifyThemePath
            firstImage {
              originalSrc
            }
          }
          variants {
            price
          }
        }
      }
    }
  `)

  const {
    relatedProducts: {
      nodes,
    },
  } = data;

  const related = data.relatedProducts;

  return (
    <Flex flexDirection={['column', 'row']}>
      {related.nodes.map((product) => {
        if (product.id !== prodId && product.productType === prodType) {
          return (
            <StyledBox width={[ 1, 1 / 4 ]} p={'1rem'} position={'relative'} key={product.id}>
              <GatsbyLink className="text-center" to={product.fields.shopifyThemePath}>
                <div style={{ height: '230px' }}>
                  <img src={product.fields.firstImage.originalSrc} alt={product.title} />
                </div>
                <Text>{product.title}</Text>
                <Text>{product.variants.price}</Text>
              </GatsbyLink>
            </StyledBox>
          )
        } else {
          ""
        }
      })}
    </Flex>
  )
}

export default ProductRelated;
