import React, { useState } from 'react';
import { Flex, Box, Text } from 'rebass';
import { Helmet } from 'react-helmet';
import loadable from '@loadable/component';

import strings from './strings';
import substrDescription from '../../utils/substrDescription.js';
import shortcodeParser from '../../utils/shortcode-parser';
import ProductCounter from '../../components/ProductCounter';
import Divider from '../../components/Divider';
import Breadcrumbs from '../../components/Breadcrumbs';
import ProductGalleryCurrentImage from './ProductGalleryCurrentImage';
import ProductGalleryThumbnails from './ProductGalleryThumbnails';
import { CurrentVariantContextProvider } from './CurrentVariantContext';
import { CurrentImageContextProvider } from './CurrentImageContext';
import ProductVariantSelector from './ProductVariantSelector';
import ProductVariantAddToCart from './ProductVariantAddToCart';
import ProductVariantPrice from './ProductVariantPrice';
import ProductVariantSku from './ProductVariantSku';
import ProductRelated from './ProductRelated';
import Tabs from 'react-bootstrap/Tabs'
import Tab from 'react-bootstrap/Tab'
import NoImage from '../../components/Icons/NoImage'
import ProductPolicy from './ProductsPolicy'
import ProductDetailsBox from './ProductDetailsBox'

// react-payment-icons-inline heavily increases webpack bundle size. Need to find alternative solution. Will disable it for now.
// const Payments = loadable(() => import('../../components/Payments'));
const ShareButtons = loadable(() => import('../../components/ShareButtons'));
const DescriptionBox = loadable(() => import('./DescriptionBox'));

const {
  productQuantityLabel,
  paymentsLabel,
  shareButtonsLabel,
  vendorLabel,
  productTypeLabel,
  relatedProductsLabel,
} = strings;

function ProductPage({ data, pageContext, location }) {
  const [currentAmount, setCurrentAmount] = useState(1);

  const {
    product: {
      title,
      id,
      description,
      descriptionHtml,
      fields: { shopifyThemePath, firstImage },
      images,
      variants,
      options,
      vendor,
      productType,
    },
  } = data;

  // There are cases when product doesn't belong to any collection.
  // In this case we need to set a guard in case "collection" and "fields" props undefined.
  const { collection } = data || {};
  const { fields } = collection || {};
  const { title: collectionTitle } = collection || {
    title: null,
  };
  const { shopifyThemePath: collectionPath } = fields || {
    shopifyThemePath: null,
  };

  const { cartUrl } = pageContext;
  const {
    payments,
    shareButtons,
    gatsbyImageProps,
  } = data.store.siteMetadata.gatsbyStorefrontConfig;

  function increaseAmount() {
    setCurrentAmount((a) => a + 1);
  }

  function decreaseAmount() {
    setCurrentAmount((a) => (a <= 1 ? 1 : a - 1));
  }

  const getShortDescription = (descriptionHtml) => {
    let shortDescrition_temp;
    return {
      withoutShortDescription: shortcodeParser.parseInContext(descriptionHtml, {
        short_description: (buf, opts) => {
          shortDescrition_temp = buf;
          return ''; // return but not using it
        },
      }),
      shortDescription: shortDescrition_temp,
    };
  };
  const { withoutShortDescription, shortDescription } = getShortDescription(
    descriptionHtml
  );

  return (
    <>
      <Helmet>
        {/* Google's meta description length is up to 920 pixels, which might
          allow for up to 158 characters. On mobile devices, the max limit is
          about 680 pixels and 120 characters. Oct 1, 2019 */}
        <meta
          name="description"
          content={
            shortDescription
              ? substrDescription(shortDescription, 158)
              : substrDescription(description, 158)
          }
        />
      </Helmet>

      {/* Breadcrumbs */}
      <Flex>
        <Breadcrumbs
          productTitle={title}
          collectionTitle={collectionTitle}
          collectionPath={collectionPath}
          separator="/"
        />
      </Flex>

      {/* Content */}
      <Flex
        flexDirection={['column', null, 'row']}
        pt={3}
        px={4}
        mx="auto"
        sx={{ maxWidth: 1300 }}
        fontFamily="body"
      >
        {/* Images */}
        <CurrentImageContextProvider>
          <Flex
            flexDirection="column"
            width={[1, null, 1 / 2]}
            px={[2, null, 3]}
          >
            <Box
              width={[1]}
              ml="auto"
              py={2}
              pr={images && images.length > 1 ? [2, null, 3] : [2, null, 3]}
              pl={images && images.length > 1 ? [2, null, 3] : [2, null, 0]}
              data-product-image-container
            >

              <ProductGalleryCurrentImage
                images={images}
                title={title}
              />
            </Box>

            {images && images.length > 1 ? (
              <Box
                width={[1]}
                py={2}
                px={[2, null, 0]}
              >
                <ProductGalleryThumbnails
                  images={images}
                  title={title}
                  gatsbyImageProps={gatsbyImageProps}
                />
              </Box>
            ) : (
              ''
            )}
          </Flex>
        </CurrentImageContextProvider>

        {/* Title and Description */}
        <Flex
          flexDirection="column"
          width={[1, null, 1 / 2]}
          px={[2, null, 3]}
          data-product-info
        >

          <CurrentVariantContextProvider>
            <Box>
              <Text fontSize={[21, 23, 30]} mb={3} data-title-box>
                {title}
              </Text>
              <ProductVariantPrice
                initialDisplayPrice={variants[0].price}
                mb={3}
              />
              {shortDescription ? (
                <DescriptionBox
                  source={shortDescription}
                  escapeHtml={false}
                  mb={3}
                />
              ) : (
                ''
              )}
            </Box>

            <ProductVariantSelector
              variants={variants}
              options={options}
              pageContext={pageContext}
              mb={4}
            />

            <Flex className="card border-left-0 border-right-0 py-5 m-0" alignItems="center" mb={4}>
              <Box className="card-deck" width={1} display={['initial', 'flex', 'flex']}>
                <Box className="rounded-0 col-sm-12 col-md-5 col-lg-4 px-md-2" mb={['1rem', 0]}>
                  <ProductCounter
                    decreaseAmount={decreaseAmount}
                    increaseAmount={increaseAmount}
                    currentAmount={currentAmount}
                  />
                </Box>
                
                <Box
                  className="rounded-0 col-md-7 col-lg-8 px-md-2"
                  width={[ 1, 3/5, 3/5]}
                  >
                  <ProductVariantAddToCart
                    amount={currentAmount}
                    cartUrl={cartUrl}
                  />
                </Box>
              </Box>
            </Flex>

            {/*<Flex mb={4}>
              <Box>
                <Text>{paymentsLabel}</Text>
                <Payments payments={payments} />
              </Box>
            </Flex>*/}

            <Divider bg="grey" mb={4} />

            <Box className="card border-0 rounded-0 m-md-0 py-sm-3" mb={4}>
              <ProductPolicy />
            </Box>

            <ProductVariantSku />
          </CurrentVariantContextProvider>

          {/* {vendor ? (
            <Flex mb={4}>
              <Box mr={2}>
                <Text>{vendorLabel}</Text>
              </Box>
              <Box>{vendor}</Box>
            </Flex>
          ) : (
            ''
          )}

          {productType ? (
            <Flex mb={4}>
              <Box mr={2}>
                <Text>{productTypeLabel}</Text>
              </Box>
              <Box>{productType}</Box>
            </Flex>
          ) : (
            ''
          )}

          <Flex mb={4} alignItems="center">
            <Box mr={2}>
              <Text>{shareButtonsLabel}</Text>
            </Box>
            <Box>
              <ShareButtons buttons={shareButtons} location={location.href} />
            </Box>
          </Flex> */}
        </Flex>
      </Flex>

      {/* Tabs */}
          <Flex className="product card border-0 px-0 col-12" fontFamily="body" mt={5} sx={{ bg: 'transparent' }}>
            <Tabs defaultActiveKey="description">
              <Tab eventKey="description" title="Description">
                <Box bg='background'
                  py={'3rem'}
                  px={'2rem'}
                  mx="auto"
                  width={1}
                >
                  <DescriptionBox
                    source={withoutShortDescription}
                    escapeHtml={false}
                  />
                </Box>
              </Tab>

              <Tab eventKey="details" title="Product Details">
                <Box bg='background'
                  py={'3rem'}
                  px={'2rem'}
                  mx="auto"
                  width={1}
                >
                  <ProductDetailsBox
                    source={withoutShortDescription}
                    escapeHtml={false}
                  />
                </Box>
              </Tab>
              
              <Tab eventKey="reviews" title="Reviews" disabled>
                <Box bg='background'
                  py={'3rem'}
                  px={'2rem'}
                  mx="auto"
                  width={1}
                >
                  <ProductDetailsBox
                    source={withoutShortDescription}
                    escapeHtml={false}
                  />
                </Box>
              </Tab>
            </Tabs>
          </Flex>

      {/* Related products */}
      <Box className='card rounded-0 border-top-0 border-bottom-0 py-5'>
        <Box className='card-body'>
          <h1 className="card-title text-center border-0">{relatedProductsLabel}</h1>
        </Box>
        <Box className='card-body px-5 py-sm-0 py-md-5'>
          <ProductRelated prodType={productType} prodId={id}/>
        </Box>
      </Box>
    </>
  );
}

export default ProductPage;
