import React, { useState } from 'react';
import { Flex, Box, Text } from 'rebass';

function ProductPolicy() {
  return (
    <div>
      <Flex
        className="card-deck"
        textAlign={'center'}
        >
        <Box width={1} className="p-2 col-md-6">
          <img src={'https://cdn.shopify.com/s/files/1/0117/9005/7534/files/prod-pol1_100x52.png'} className="card-img-top col-sm-10 col-md-12 col-lg-8 py-sm-3 p-md-2" />
          <Text className="card-text mx-auto">Returns within 14 days</Text>
        </Box>

        <Box width={1} className="p-2 col-md-6">
          <img src={'https://cdn.shopify.com/s/files/1/0117/9005/7534/files/prod-pol2_100x52.png'} className="card-img-top col-sm-10 col-md-12 col-lg-8 py-sm-3 p-md-2" />
          <Text className="card-text mx-auto">Made in France</Text>
        </Box>
      </Flex>

      <Flex
        className="card-deck"
        textAlign={'center'}
        >
        <Box width={1} className="p-2 col-md-6">
          <img src={'https://cdn.shopify.com/s/files/1/0117/9005/7534/files/prod-pol3_100x52.png'} className="card-img-top col-sm-10 col-md-12 col-lg-8 py-sm-3 p-md-2" />
          <Text className="card-text mx-auto">Customer service 9am to 6pm</Text>
        </Box>

        <Box width={1} className="p-2 col-md-6">
          <img src={'https://cdn.shopify.com/s/files/1/0117/9005/7534/files/prod-pol4_100x52.png'} className="card-img-top col-sm-10 col-md-12 col-lg-8 py-sm-3 p-md-2" />
          <Text className="card-text mx-auto">US free Delivengo shipping on orders over $100</Text>
        </Box>
      </Flex>
    </div>
  );
}

export default ProductPolicy;